@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
:root {
  --primary-color: #292cd1;
  --off-white: #f3f4f6;

  --dark-gray: #6b7280;
}

.App {
  font-family: "Montserrat", sans-serif;
  text-align: center;
}

h1 {
  background-color: #292cd1;
  background-color: var(--primary-color);
  color: #f3f4f6;
  color: var(--off-white);
  margin: 0px;
}
button {
  margin: 1rem;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  background-color: #292cd1;
  background-color: var(--primary-color);
  color: #f3f4f6;
  color: var(--off-white);
}
.blocks {
  text-align: center;
}
.non-bullet {
  list-style: none;
}
.list {
  border: 2px solid #292cd1;
  border: 2px solid var(--primary-color);
  margin: 1rem;
  border-radius: 1rem;
  padding: 1rem;
  background-color: skyblue;
}
.names {
  font-weight: bold;
}

